form {
    display: flex;
    padding: 10px;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.input {
    margin-right: 10px;
}

.frame {
    width: 40vw;
    max-width: 400px;
}

.header {
    display: inline;
    text-align: center;
}

.list-wrapper {
    max-height: 600px;
    overflow-y: auto;
}

.list-item {
    text-align: left;
    padding: 10px;
}

@media screen and (max-width: 768px) {
    .frame {
        width: 80%;
    }
}